@import "../../variables";

.contact {
  display: flex;
  flex-direction: column;
}

.contact-h1 {
  font-size: 34px;
}

.icon {
  height: 50px;
  width: 50px;
  margin-bottom: 40px;
}

.contact-main {
  display: flex;
  justify-content: space-around;
}

.contact-div {
  display: flex;
  // justify-content: space-between;
}

.details {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
  .headers {
    margin: 0;
    color: $sub-text;
    font-family: MomcakeThin;
  }
  .email-link, .contact-content {
    color: $main-text;
    font-size: 24px;
    margin: 0;
  }
  .email-link:hover {
    color: $highlighter;
  }
}

.i-twitter {
	-webkit-animation: i-twitter 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: i-twitter 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

 @-webkit-keyframes i-twitter {
  0% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes i-twitter {
  0% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.i-linkedin {
	-webkit-animation: i-linkedin 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.5s both;
