.contactform {
	-webkit-animation: contactform 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: contactform 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

 @-webkit-keyframes contactform {
  0% {
    -webkit-transform: rotateY(-30deg) translateY(-300px) skewY(30deg);
            transform: rotateY(-30deg) translateY(-300px) skewY(30deg);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotateY(0deg) translateY(0) skewY(0deg);
            transform: rotateY(0deg) translateY(0) skewY(0deg);
    opacity: 1;
  }
}
@keyframes contactform {
  0% {
    -webkit-transform: rotateY(-30deg) translateY(-300px) skewY(30deg);
            transform: rotateY(-30deg) translateY(-300px) skewY(30deg);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotateY(0deg) translateY(0) skewY(0deg);
            transform: rotateY(0deg) translateY(0) skewY(0deg);
    opacity: 1;
  }
}

.contact-h1 {
	-webkit-animation: contact-h1 2s cubic-bezier(0.390, 0.575, 0.565, 1.000) 1s both;
	        animation: contact-h1 2s cubic-bezier(0.390, 0.575, 0.565, 1.000) 1s both;
}

 @-webkit-keyframes contact-h1 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes contact-h1 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
